import { LogLevel } from "@bcp/shared/common";
import { Message } from "@bcp/shared/messaging";
export function logError(context, message, details, otherTabId) {
  var tabId = otherTabId || context.session.documentId;
  context.sendMessage(new Message({
    type: "log",
    payload: {
      tabId: tabId,
      level: LogLevel.error,
      message: message,
      details: details
    }
  }));
}
