export function getOperationType(document, operationName) {
  var operationNodes = document.definitions.map(function (d) {
    return d;
  }).filter(function (o) {
    return !!o;
  });
  var operationNode = operationNodes.length === 1 ? operationNodes[0] : operationNodes.find(function (o) {
    return o.name && o.name.value === operationName || false;
  });
  return operationNode ? operationNode.operation : "query";
}