import { Message } from "@bcp/shared/messaging";
export function addHistoryItem(context, documentId, operationFailed, operationType, operationName) {
  context.sendMessage(new Message({
    type: "new-history-item",
    payload: {
      documentId: documentId,
      operationFailed: operationFailed,
      operationName: operationName,
      operationType: operationType
    }
  }));
}